import $http from '../index'

// 
export function getInvestinfo(page = 1, pageSize = 20) {
    return $http({
        url: `/pattern/invest/investinfo/${page}/${pageSize}`,
        method: 'get',
    })
}

// 
export function transtoration(param) {
    return $http({
        url: '/pattern/invest/transtoration',
        method: 'post',
        data: param,
    })
}

